/** @author 薛鑫欣
 * @description 分销记录
*/
<template>
  <div>
    <div id="screen">
      <div style="  display: flex;">
      <div class="screen_item" >
        <label for >上级用户：</label>
        <el-input v-model="up_user" placeholder="" ></el-input>
      </div>
      <div class="screen_item" >
        <label for>下级用户：</label>
        <el-input v-model="down_user" placeholder=""></el-input>
      </div>
      <div class="screen_item" >
        <label for>关系建立时间：</label>
        <el-date-picker
          v-model="startTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
        <span style="line-height:40px;font-size:20px;color:#333">—</span>
        <el-date-picker
          v-model="endTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </div>
      </div>
      <div style="display: flex;margin-top:20px">
      <div class="screen_item" id="buttons">
        <el-button @click="querybtnClick" type="primary" style="width:112px">查询</el-button>
        <el-button @click="cleanbtnClick">清除筛选项</el-button>
      </div>
      </div>
    </div>
     <!-- 数据统计 -->
    <div id="table_header">
      <label for id="total" >{{'数据统计：共' + num + '条'}}</label>
      <!-- <el-button id="add" @click="addbtnClick" type="primary">添加参赛人员</el-button> -->
    </div>
    <div id="table">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="id" label="强关系编号" align="center"></el-table-column>
        <!-- 上级 -->
        <el-table-column prop="up_id" label="上级用户ID" align="center"></el-table-column>
        <el-table-column prop="up_name" label="上级用户名称" align="center"></el-table-column>
        <el-table-column prop="up_type" label="上级用户类型" align="center"></el-table-column>
        <el-table-column prop="up_phone" label="上级用户电话" align="center"></el-table-column>
        <!-- 下级 -->
        <el-table-column prop="down_id" label="下级用户ID" align="center"></el-table-column>
        <el-table-column prop="down_name" label="下级用户名称" align="center"></el-table-column>
        <el-table-column prop="down_type" label="下级用户类型" align="center"></el-table-column>
        <el-table-column prop="down_phone" label="下级用户电话" align="center"></el-table-column>
       <!-- ----- -->
        <el-table-column prop="date" label="关系建立时间" align="center"></el-table-column>
        <el-table-column prop="earnings" label="分销总收益" align="center"></el-table-column>
        <el-table-column prop="relation_status" label="关系状态" align="center"></el-table-column>
        <el-table-column prop="dis_details" label="分销详情" align="center">
          <template slot-scope="scope">
            <el-button
              @click="jumpDetails(scope.row)"
              type="text"
              size="small"
            >查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div id="table_bottom">
        <el-pagination
          @current-change="handleCurrentChange"
          next-text="下一页"
          prev-text="上一页"
          layout="prev, pager, next"
          :total=total>
        </el-pagination>
      </div>
  </div>
</template>
<script>
export default {
  name: 'manageDistribution',
  data () {
    return {
      num: 0,
      up_user: '',
      down_user: '',
      startTime: '',
      endTime: '',
      page: 1,
      page_size: 10,
      total: 0,
      tableData: [
        {
          id: 'FDH03928432933',
          up_id: '213091',
          up_name: '张三',
          up_type: '123',
          up_phone: '123456',
          down_id: '213091',
          down_name: '李四',
          down_type: '456',
          down_phone: '123456',
          earnings: '99999',
          date: '是否',
          relation_status: '手动阀'
        }
      ]
    }
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['分销管理', '分销列表'])
  },
  mounted () {
    this.activity_id = this.$route.params.id
    this.getLeaderList()
  },
  methods: {
    // 跳到详情页面
    jumpDetails (val) {
      this.$router.push('/distributionDetails/' + 1)
    },
    // 点击查询
    querybtnClick () {
      console.log('querybtnClick')
      this.getLeaderList()
    },
    // 清除查询内容
    cleanbtnClick () {
      console.log('cleanbtnClick')
      this.up_user = ''
      this.down_user = ''
      this.startTime = ''
      this.endTime = ''
      this.getLeaderList()
    },
    // 点击切换页面
    handleCurrentChange (val) {
      this.page = val
      this.getLeaderList()
    },
    // 性别转换
    genderMethod (index) {
      return this.tableData[index].union_user_object.gender === 'male' ? '男' : '女'
    },
    // 职位为空的话是会员
    positionMethod (index) {
      return this.tableData[index].union_user_object.work_position ? this.tableData[index].union_user_object.work_position : '会员'
    },
    // 得到领队列表
    getLeaderList () {
      // let _self = this
      // var params = `?activity_id=${this.activity_id}&page=${_self.page}&per_page=${_self.page_size}`
      // if (_self.name !== '') {
      //   params += `&name=${this.name}`
      // }
      // if (_self.union !== '') {
      //   params += `&union=${this.union}`
      // }
      // if (_self.phone !== '') {
      //   params += `&phone=${this.phone}`
      // }
      // leaderList(params)
      //   .then(data => {
      //     this.tableData = data.data.items
      //     this.total = data.data.total
      //   }).catch(function (e) {
      //   })
    }
  }
}
</script>
<style scoped>
.el-table >>> th {
  background-color: #e3eefa;
  border-left: #9FAFCC solid 1px;
  font-family: "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53";
  color: #000000;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  border:none;
}
#screen {
  text-align: left;
  overflow: hidden;
  margin: 10px auto;
}
.screen_item {
  width: 25%;
  float: left;
  padding-right: 20px;
  padding-bottom: 5px;
  box-sizing: border-box;
  display: flex;
}
.screen_item label {
  line-height: 40px;
}
.screen_item .el-input {
  flex: 1;
}
#add {
  float: right;
}
#table {
  margin-top: 30px;
}
#table_bottom {
  margin-top: 10px;
}
#table_header {
  overflow: hidden;
}
#total {
  float: left;
  margin-top: 18px;
  padding: 0px 5px;
  color: white;
  background-color: #80abe1;
  float: left;
  border-radius: 3px;
}
</style>



